/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
.vdl-top-bar {
  height: 59px;
  background-color: #f9f9f9;
  border-bottom: solid 1px #c2c2c2;
  flex-direction: row;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between; }
  .vdl-top-bar .vdl-top-bar-logo {
    width: 40px;
    height: 60px;
    padding-left: 10px; }
  .vdl-top-bar .vdl-top-bar-title {
    font-family: Roboto, sans-serif;
    color: #414142;
    font-size: 22px;
    padding: 20px 30px 20px 10px;
    line-height: 22px;
    min-width: 220px;
    flex: 1 1 auto;
    margin-right: 15px; }
  .vdl-top-bar vdl-input-container {
    margin-top: 3px;
    flex: 1 1 auto;
    margin-left: 15px;
    margin-right: 20px; }
    .vdl-top-bar vdl-input-container .vdl-input-element {
      margin-top: 0;
      margin-bottom: 8px; }
    .vdl-top-bar vdl-input-container .vdl-input-underline {
      margin-top: 11px; }
    .vdl-top-bar vdl-input-container .vdl-input-underline.vdl-focused {
      border-top-width: 2px;
      margin-top: 10px; }
  .vdl-top-bar .vdl-top-bar-actions {
    display: flex;
    align-self: center;
    border-left: dotted 1px black; }
    .vdl-top-bar .vdl-top-bar-actions > button {
      padding: 0 20px;
      margin: 0 20px;
      flex: 0 1 auto;
      align-self: center; }
  .vdl-top-bar button[vdl-icon-button] {
    text-align: center;
    font-size: 20px; }
    .vdl-top-bar button[vdl-icon-button] .vdl-search {
      padding-right: 20px; }
    .vdl-top-bar button[vdl-icon-button] .vdl-search-top-bar {
      padding-right: 40px; }
    .vdl-top-bar button[vdl-icon-button] vdl-icon {
      cursor: pointer;
      height: 20px;
      width: 20px;
      font-size: 20px;
      line-height: 20px; }
